.App {
  flex-direction: row;
  justify-content: center;
  align-items: start;
  column-gap: 20px;
  padding: 20px;
  display: flex;
}

table {
  border-collapse: separate;
  border-spacing: 0;
  font-size: 14px;
}

thead {
  z-index: 2;
  position: sticky;
  top: -1px;
}

thead tr:first-child th {
  border-top: 1px solid var(--gray-2);
}

th {
  background: var(--gray-1);
}

th, td {
  border-right: 1px solid var(--gray-2);
  border-bottom: 1px solid var(--gray-2);
}

tr th:first-child, tr td:first-child {
  border-left: 1px solid var(--gray-2);
}

thead tr:nth-child(2) th:first-child, tr td:first-child {
  z-index: 1;
  position: sticky;
  left: -1px;
}

th, td {
  text-align: right;
  white-space: nowrap;
  background: #fff;
  padding: 10px;
  overflow: visible;
}

tbody tr:nth-child(2n) td {
  background: var(--gray-1);
}

tbody tr:hover td {
  background: var(--blue-1);
}

:root {
  --gray-1: #f5f5f4;
  --gray-2: #e7e5e4;
  --blue-1: #eff6ff;
}

html {
  color-scheme: light;
  font-family: system-ui;
}
